<template>
  <div class="nextforgot">
    <div class="topBgcolor"></div>

    <template>
      <div class="login">
        <!--登录页面 -->
        <div class="login-box">
          <!-- 顶部导航 -->
          <van-nav-bar>
            <template #left>
              <div class="left-text">
                <div class="logo">
                  <img
                    class="auto-img"
                    src="../assets/images/home_active.png"
                    alt=""
                  >
                </div>
                <div class="logo-title">Luckin Coffce</div>
              </div>
            </template>
            <template #right>
              <div class="right-text">
                <div @click="goPre">返回上一步</div>
              </div>
            </template>
          </van-nav-bar>

          <!--  -->
          <div class="login-form">
            <div class="title">忘记密码！</div>
            <div class="en-title">Forget the password!</div>

            <!-- 表单 -->
            <div class="login-form-box">
              <van-form>
                <van-field
                  class="border-bottom"
                  label="手机号："
                  placeholder="手机号"
                  readonly
                  v-model="userInfo.phone"
                />
                <van-field
                  class="border-bottom"
                  :type="isPassword ? 'password' : 'text'"
                  label="新密码："
                  placeholder="新密码"
                  v-model="userInfo.password"
                  :right-icon="isPassword ? 'closed-eye' : 'eye-o'"
                  @click-right-icon="viewPassword"
                />
                <div
                  class="clearfix forgot"
                  @click="goLogin"
                >
                  <span class="fr">已有帐号，立即登录</span>
                </div>
                <div class="login-btn">
                  <van-button
                    block
                    type="info"
                    native-type="submit"
                    @click="commit"
                  >提&nbsp;&nbsp;交</van-button>
                </div>
              </van-form>
            </div>
          </div>
        </div>
      </div>
    </template>

  </div>
</template>

<script>
import "../assets/less/nextforgot.css";
import { validForm } from "../assets/js/validForm";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      userInfo: {
        phone: "",
        password: "",
      },
      isPassword: true,
    };
  },

  created() {
    this.isnull();
    this.userInfo.phone = this.$route.query.phone;
  },

  methods: {
    // 返回登录
    goLogin() {
      this.$router.push({ name: "Login" });
    },

    // 返回上一步
    goPre() {
      this.$router.go(-1);
    },

    // 如果phone为空，则跳转到登录页
    isnull() {
      if (this.$route.query.phone == "") {
        this.$router.push({ name: "Login" });
      }
    },

    // 查看密码
    viewPassword() {
      this.isPassword = !this.isPassword;
    },

    // 提交  修改密码
    commit() {
      //   验证修改密码表单
      let o = {
        phone: {
          value: this.userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "请输入11位手机号码",
        },
        password: {
          value: this.userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "请输入5~15位以字母开头的密码",
        },
      };

      if (!validForm.valid(o)) {
        //   如果验证不通过
        return;
      }

      // 发起修改密码请求
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 图标
        loadingType: "spinner",
        // 显示时间，如果时间位0，则不会自动关闭
        duration: 0,
      });

      //   序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        phone: this.userInfo.phone,
        password: this.userInfo.password,
      });

      this.axios({
        method: "POST",
        //   请求参数
        url: "/retrievePassword",
        data,
      })
        .then((result) => {
          //   关闭加载提示;
          this.$toast.clear();
          

          if (result.data.code == "L001") {
            this.$toast("修改密码成功，即将返回登录");
            setTimeout(() => {
              this.$router.push({ name: "Login" });
            }, 2000);
          } else {
            this.$notify({ message: result.data.msg, type: 'warning' });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>